* {
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
  font-family: 'Roboto', -apple-system, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

i{
  img{
    width: 12px;
    height: 12px;
  }
}

.button{
  font-size: 14px;
  background-color: #4DD599;
  border-radius: 4px;
  color: #FFF;
  border: 0;
  padding: 10px 20px;
  cursor: pointer;

  &:hover {
      background-color: darken(#4DD599, 6%);
  }

  &:active {
      background-color: darken(#4DD599, 10%);
  }

  &:disabled {
    background-color: #d8d8d8;
  }

  &--grey {
    color: #828282;
    background-color: #f4f6f8;

    &:hover {
      background-color: darken(#f4f6f8, 5%);
    }

    &:active {
      background-color: darken(#f4f6f8, 10%);
    }
  }
}

.field {
  font-size: 14px;
  background: #FFFFFF;
  border: 1px solid #efefef;
  border-radius: 4px;
  padding: 8px 12px;
  width: 100%;
  box-sizing: border-box;

  &:focus {
    border-color: #bdbdbd;
  }
}

.todo {
  display: flex;
  position: absolute;
  left: 50%;
  top: 50%;
  width: calc(100vw - 70px);
  height: calc(100vh - 100px);

  transform: translate(-50%, -50%);
  box-shadow: 1px 2px 20px #f3f3f3;
  border-radius: 10px;
  border: 1px solid #f1f1f1;
  font-size: 14px;

  &__sidebar {
    background-color: #f4f6f8;
    width: 200px;
    height: calc(100% - 120px);
    border-right: 1px solid #f1f1f1;
    padding: 60px 20px;
  }

  &__tasks {
    flex: 1;
    padding: 60px;
    overflow: auto;
  }
}